.mainContainer {
  display: inline-block;
  padding: 20px;
  z-index: 10;
  position: absolute;
  height: 50%;
  left:50%;
  top:50%;
  transform: translate(-50%,-50%);
  border-radius: 8px;
  border: solid 6px #FFD01E;
  background-color: #FFFFFF;
  text-align: center;
  font-family: Pokemon GB, sans-serif;
}

.mainContainer > h2 {
	display:block;
	line-height: 1.5;
	position: relative;
	top:35%;
	transform: translateY(-35%);
	text-align: center;
}

.go {
	width:300px;
	display:block;
	position: relative;
	margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  padding-bottom: 15px;
 	border-radius: 5px;
	top:40%;
	transform: translateY(-40%);
	border-radius: 5px;
  background-color: #ffd01e;
  box-shadow: 0 20px 20px -12px ;
  transition: box-shadow 200ms ease, background-color 200ms ease;
  font-weight: 700;
  text-align: center;
}
